/* global console, document, Office */

Office.onReady(info => {
  if (info.host === Office.HostType.PowerPoint) {
    document.getElementById("app-body").style.display = "flex";
    document.getElementById("run").onclick = insertSampleFile;
  }
});

const sampleFileBase64 = "";

export async function  insertSampleFile() {
  Office.context.application.createDocumentAsync({base64: sampleFileBase64}, function (asyncResult) {
    if (asyncResult.status == Office.AsyncResultStatus.Failed) {
      console.log(asyncResult.error.name, asyncResult.error.message);
      document.getElementById("error-content").innerHTML = asyncResult.error.name 
        + ": " + asyncResult.error.message + " Error code: " + asyncResult.error.code;
      document.getElementById("error-content").style.display = "block";
    } else {
      document.getElementById("error-content").style.display = "none";
      console.log("Success", "Success");
    }
  });
}
